import React from "react";
import s from "./OrderSummary.module.sass";
import { connect } from "react-redux";
import { Card } from "primereact/card";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";

const header = src => <img alt="Card" src={src} />;
const footer = ({ removeGood, good }) => (
	<span>
		<Button
			onClick={() => removeGood(good.id)}
			label="Cancel"
			icon="pi pi-times"
			className="p-button-secondary"
		/>
	</span>
);
class OrderSummary extends React.Component {
	state = {};
	handleKey = key => event => {
		this.setState({ [key]: event.target.value });
	};

	changeMenu = () => {
		this.setState({ showMenu: !this.state.showMenu });
	};

	render() {
		// const Link = this.props.Link;
		const { cartItems, removeGood } = this.props;

		return (
			<>
				<Card className={s.CustomerDetails}>
					<h2>1.OrderSummary </h2>

					<hr />

					<ul className={s.List}>
						{cartItems.length === 0 ? (
							<li>Пусто</li>
						) : (
							cartItems.map((item, key) => {
								return (
									<li key={key}>
										<Card
											footer={footer({
												removeGood,
												good: item
											})}
											header={header(item.image)}
										>
											{item.name} {item.price}
										</Card>
									</li>
								);
							})
						)}
					</ul>

					<hr />

					<div>
						<div>Промежуточный итог</div>

						<div>Общая сумма</div>
					</div>
				</Card>
			</>
		);
	}
}

const a = state => ({
	cartItems: state.cart.preparedGoods
});

const b = dispach => {
	return {
		removeGood: _id => dispach({ type: "REMOVE_GOOD", payload: _id })
	};
};

export default connect(
	a,
	b
)(OrderSummary);
