import React from "react";
import { connect } from "react-redux";
import s from "./GoodFoto.module.sass";
import { InputText } from "primereact/inputtext";
import Menu from "../Menu/";
import { Lightbox } from "primereact/lightbox";

class GoodFoto extends React.Component {
	state = {};

	render() {
		const { photo } = this.props;
		return (
			<div className={s.GoodFoto}>
				<Lightbox
					type="images"
					images={[
						{
							source: photo,
							thumbnail: photo,
							title: "Goods Preview"
						}
					]}
				/>
			</div>
		);
	}
}

const a = state => state;
const b = dispach => {
	return {
		changeMenu: () => dispach({ type: "CHANGE_MENU_TYPE" })
	};
};

export default connect(
	a,
	b
)(GoodFoto);
