import React from "react";
import { connect } from "react-redux";
import s from "./GoodDescription.module.sass";
import { InputText } from "primereact/inputtext";
import Menu from "../Menu/";
import { Lightbox } from "primereact/lightbox";

class GoodDescription extends React.Component {
	state = {};

	render() {
		const { describe = "", characteristics = "" } = this.props;

		return (
			<div className={s.GoodDescription}>
				<div className={s.Description}> {describe} </div>
				<div className={s.Characteristics}> {characteristics} </div>
			</div>
		);
	}
}

const a = state => state;
const b = dispach => {
	return {
		changeMenu: () => dispach({ type: "CHANGE_MENU_TYPE" })
	};
};

export default connect(
	a,
	b
)(GoodDescription);
