import React from "react";
import Goods from "../components/Goods/";
import { connect } from "react-redux";
import Filters from "../components/Filters/";

const s = {
	PageFilter: {
		display: "flex"
	}
};

class Filter extends React.Component {
	state = {};

	render() {
		const { history } = this.props;

		return (
			<div style={s.PageFilter}>
				<Filters />
				<Goods history={history} />
			</div>
		);
	}
}
const a = state => ({
	userData: state.user
});

const b = dispach => {
	return {
		updateUserData: payload => dispach({ type: "UPDATE_USER", payload })
	};
};

export default connect(
	a,
	b
)(Filter);
