import React from "react";
import { connect } from "react-redux";
import s from "./GoodCounter.module.sass";
import { InputText } from "primereact/inputtext";
import Menu from "../Menu/";
import { Lightbox } from "primereact/lightbox";

class GoodCounter extends React.Component {
	state = {
		count: 1
	};
	handleClick = type => () => {
		if (type === "plus") {
			this.setState({ count: this.state.count + 1 });
		} else {
			this.setState({
				count: this.state.count !== 1 ? this.state.count - 1 : 1
			});
		}
	};

	handleAddToCard = () => {
		const { addGood, good } = this.props;
		const { count } = this.state;
		for (let i = 0; i < count; i++) {
			addGood(good);
		}
	};

	render() {
		const { name = "", price = 0 } = this.props;
		const { count = 0 } = this.state;
		return (
			<div className={s.GoodCounter}>
				<h2>{name}</h2>
				<div className={s.Price}> ${price} </div>
				<div className={s.Counter}>
					<button onClick={this.handleClick("minus")}>-</button>
					<span> {count} </span>
					<button onClick={this.handleClick("plus")}> + </button>
				</div>

				<button onClick={this.handleAddToCard} className={s.AddToCArd}>
					{" "}
					Add to Cart{" "}
				</button>
			</div>
		);
	}
}

const a = state => ({});
const b = dispatch => {
	return {
		addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good })
	};
};

export default connect(
	a,
	b
)(GoodCounter);
