import React from "react";
import Header from "../components/Header/";
import Goods from "../components/Goods/";
import Slider from "../components/Slider/";
import BurgerMenu from "../components/BurgerMenu/";
import { Button } from "primereact/button";
import Login from "./Login.jsx";
import Register from "./Register.jsx";
import Main from "./Main.jsx";
import { connect } from "react-redux";
import localForage from "localforage";
import Cart from "../components/Cart/";
import Checkout from "./Checkout.jsx";
import Goodinfo from "./Goodinfo.jsx";
import Profile from "./Profile.jsx";
import Footer from "../components/Footer/";
import Filter from "./Filter.jsx";
import ProductsService from "../services/Products";

import {
	Route,
	Link,
	Switch,
	BrowserRouter
	// withRouter,
} from "react-router-dom";

class Routing extends React.Component {
	state = {};

	async componentWillMount() {
		const { userData, updateUserData, setGoods } = this.props;
		const goodList = await ProductsService.get();
		setGoods(goodList);

		if (!userData.token) {
			const saveuserData = await localForage.getItem("userData");

			updateUserData(saveuserData);
		}
	}

	render() {
		const { history } = this.props;
		return (
			<BrowserRouter>
				<main>
					<Header Link={Link} />
					<BurgerMenu />

					<div
						style={{
							height: "150px",
							width: "100%"
						}}
					></div>
					<Switch>
						<Route
							exact
							path="/"
							component={() => (
								<Main Link={Link} history={history} />
							)}
						/>

						<Route
							exact
							path="/checkout"
							component={() => <Checkout Link={Link} />}
						/>
						<Route
							exact
							path="/login"
							component={() => <Login Link={Link} />}
						/>
						<Route
							exact
							path="/register"
							component={() => <Register Link={Link} />}
						/>
						<Route
							exact
							path="/good/:id"
							component={() => <Goodinfo Link={Link} />}
						/>
						<Route
							exact
							path="/profile"
							component={() => <Profile Link={Link} />}
						/>
						<Route
							exact
							path="/filter/:category"
							component={() => <Filter Link={Link} />}
						/>
					</Switch>
					<Cart Link={Link} />
					<Footer Link={Link} />
				</main>
			</BrowserRouter>
		);
	}
}
const a = state => ({
	userData: state.user
});

const b = dispach => {
	return {
		updateUserData: payload => dispach({ type: "UPDATE_USER", payload }),
		addGood: payload => dispach({ type: "ADD_GOOD", payload }),
		setGoods: payload => dispach({ type: "SET_GOODS", payload })
	};
};

export default connect(
	a,
	b
)(Routing);
