import React from "react";
import { connect } from "react-redux";
import localForage from "localforage";
import UsersService from "../services/Users";

class Profile extends React.Component {
	state = {};

	handleExit = () => {
		const { updateUserData } = this.props;
		localForage.setItem("userData", null);
		updateUserData(null);
	};

	async componentDidMount() {
		const { userData, updateUserData } = this.props;
		console.log(userData.id);

		if (userData.token && !userData.createdAt) {
			const userResult = await UsersService.get(
				userData.id,
				userData.token
			);

			updateUserData({
				email: userResult.email,
				name: userResult.name || "",
				city: userResult.city || "",
				address: userResult.address || "",
				phone: userResult.phone || "",
				createdAt: userResult.createdAt
			});
		}
	}

	render() {
		const { Link = {}, userData } = this.props;

		return (
			<>
				<div>
					Имя: {userData.name},
					<br />
					Электронная почтa: {userData.email},
					<br />
					Телефон: {userData.phone}, <br />
					День рождения: {userData.dateOfBirth}, <br />
					Город: {userData.city}, <br />
					Адрес: {userData.address},
				</div>
				<Link to="/">
					<button onClick={this.handleExit}>Выход</button>
				</Link>
			</>
		);
	}
}

const a = store => ({
	userData: store.user
});
const b = dispatch => {
	return {
		updateUserData: payload => dispatch({ type: "UPDATE_USER", payload })
	};
};
export default connect(
	a,
	b
)(Profile);
