import React from "react";
import s from "./CustomerDetails.module.sass";
import { connect } from "react-redux";
import { Card } from "primereact/card";
import { InputText } from "primereact/inputtext";
import { RadioButton } from "primereact/radiobutton";
class Menu extends React.Component {
	state = {
		showMenu: false,
		active: 0,
		email: "",
		mobile: "",
		shipping: "free",
		payments: "paypal"
	};
	handleKey = key => event => {
		this.setState({ [key]: event.target.value });
	};

	changeMenu = () => {
		this.setState({ showMenu: !this.state.showMenu });
	};

	render() {
		const Link = this.props.Link;

		return (
			<>
				<Card className={s.CustomerDetails}>
					<h2>1.CustomerDetails </h2>
					<div className={s.inputs}>
						<span className="p-float-label">
							<InputText
								name="email"
								id="in"
								value={this.state.email}
								onChange={this.handleKey("email")}
							/>
							<label htmlFor="in">Email</label>
						</span>
						<span className="p-float-label">
							<InputText
								name="phone"
								id="in"
								value={this.state.phone}
								onChange={this.handleKey("phone")}
							/>
							<label htmlFor="in">Phone</label>
						</span>
						<h4>Shipping To</h4>
						<span className="p-float-label">
							<InputText
								id="in"
								value={this.state.country}
								onChange={this.handleKey("country")}
							/>
							<label htmlFor="in">Country</label>
						</span>
						<span className="p-float-label">
							<InputText
								id="in"
								value={this.state.state}
								onChange={this.handleKey("state")}
							/>
							<label htmlFor="in">State</label>
						</span>
						<span className="p-float-label">
							<InputText
								id="in"
								value={this.state.city}
								onChange={this.handleKey("city")}
							/>
							<label htmlFor="in">City</label>
						</span>
						<span className="p-float-label">
							<InputText
								id="in"
								value={this.state.pay}
								onChange={this.handleKey("pay")}
							/>
							<label htmlFor="in">Pay</label>
						</span>
					</div>
					<div className={s.radio}>
						<Card className={s.shipping}>
							<RadioButton
								value={"free"}
								name="city"
								onChange={this.handleKey("shipping")}
								checked={this.state.shipping === "free"}
							/>
							<div className={s.radioCard}>
								<span>
									Free shipping
									<i>2-4 days</i>
								</span>
								<span>$0.00</span>
							</div>
						</Card>
						<Card className={s.shipping}>
							<RadioButton
								value={"local"}
								name="city"
								onChange={this.handleKey("shipping")}
								checked={this.state.shipping === "local"}
							/>
							<div className={s.radioCard}>
								<span>
									Local Pickup
									<i>Store A and Store B open 24h/7d</i>
								</span>
								<span>$0.00</span>
							</div>
						</Card>
						<Card className={s.shipping}>
							<RadioButton
								value={"express"}
								name="city"
								onChange={this.handleKey("shipping")}
								checked={this.state.shipping === "express"}
							/>
							<div className={s.radioCard}>
								<span>
									DHL Express
									<i>1 day</i>
									<span>$0.00</span>
								</span>
							</div>
						</Card>
					</div>

					<div className={s.radio}>
						<Card className={s.shipping}>
							<RadioButton
								value={"free"}
								name="city"
								onChange={this.handleKey("shipping")}
								checked={this.state.shipping === "free"}
							/>
							<div className={s.radioCard}>
								<span>
									Free shipping
									<i>2-4 days</i>
								</span>
								<span>$0.00</span>
							</div>
						</Card>
						<Card className={s.shipping}>
							<RadioButton
								value={"local"}
								name="city"
								onChange={this.handleKey("shipping")}
								checked={this.state.payments === "paypal"}
							/>
							<div className={s.radioCard}>
								<span>
									Local Pickup
									<i>Store A and Store B open 24h/7d</i>
								</span>
								<span>$0.00</span>
							</div>
						</Card>
						<Card className={s.shipping}>
							<RadioButton
								value={"express"}
								name="city"
								onChange={this.handleKey("shipping")}
								checked={this.state.shipping === "express"}
							/>
							<div className={s.radioCard}>
								<span>
									DHL Express
									<i>1 day</i>
									<span>$0.00</span>
								</span>
							</div>
						</Card>
					</div>
				</Card>
			</>
		);
	}
}

const a = state => ({
	showMenu: state.showMenu
});

const b = dispach => {
	return {
		changeMenu: () => dispach({ type: "CHANGE_MENU_TYPE" })
	};
};

export default connect(
	a,
	b
)(Menu);
