import React from "react";
import s from "./Goods.module.sass";
import { connect } from "react-redux";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import localForage from "localforage";
import { Link } from "react-router-dom";

const footer = ({ good, addGood, removeGood }) => {
	const saveGood = async () => {
		const Goods = (await localForage.getItem("Goods")) || [];
		const newGood = [...Goods, good];
		localForage.setItem("Goods", newGood);
	};
	const deleteGood = async () => {
		const Goods = (await localForage.getItem("Goods")) || [];
		const newGood = Goods.filter(_good => _good.id !== good.id);
		localForage.setItem("Goods", newGood);
	};
	return (
		<span>
			<Button
				label="Купить"
				icon="pi pi-check"
				style={{ marginRight: ".25em" }}
				onClick={() => {
					addGood(good);
					saveGood();
				}}
			/>
			<Button
				label="Убрать с корзины"
				icon="pi pi-times"
				className="p-button-secondary"
				onClick={() => {
					removeGood(good.id);
					deleteGood();
				}}
			/>
		</span>
	);
};

class Goods extends React.Component {
	state = {};

	componentDidMount() {
		const { clearFilter, cleanFilter } = this.props;
		if (clearFilter) {
			cleanFilter();
		}
	}

	render() {
		const {
			goodsList,
			addGood,
			removeGood,
			cartItems = [],
			brands = [],
			years = [],
			sizes = [],
			rangeValues
		} = this.props;
		const category = window.location.pathname.split("/")[2];

		return (
			<div className={s.GoodsGrid}>
				{goodsList
					.filter(good => {
						if (
							brands.length === 0 &&
							sizes.length === 0 &&
							years.length === 0 &&
							rangeValues[0] === 500 &&
							rangeValues[1] === 10000
						) {
							return true;
						}

						const checkBrand =
							brands.includes(good.brand) || brands.length === 0;
						const checkSizes =
							good.size.find(_size => {
								return sizes.includes(_size);
							}) || sizes.length === 0;
						const checkYears =
							years.includes(good.year) || years.length === 0;
						const checkPrice =
							good.price >= rangeValues[0] &&
							good.price <= rangeValues[1];
						return (
							checkBrand && checkSizes && checkYears && checkPrice
						);

						// return false;
					})
					.filter(good =>
						category && category == good.category
							? good
							: !category
							? good
							: false
					)

					.map((good, key) => {
						const header = (
							<Link to={`/good/${good.id}`}>
								<img
									className={s.goodImg}
									alt="Card"
									src={good.image}
								/>
							</Link>
						);
						return (
							<Card
								className={s.pCard}
								key={key}
								footer={footer({ good, addGood, removeGood })}
								header={header}
							>
								{good.name}
								<i> {good.price}$ </i>
								<span className={s.buyCount}>
									{cartItems.filter(x => x.id === good.id)
										.length || ""}
								</span>
							</Card>
						);
					})}
			</div>
		);
	}
}

const a = store => ({
	goodsList: store.goods.goodsList,
	cartItems: store.cart.preparedGood,
	brands: store.filter.brands,
	years: store.filter.years,
	sizes: store.filter.sizes,
	rangeValues: store.filter.rangeValues,
	category: store.filter.category
});
const b = dispatch => {
	return {
		addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
		removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id }),
		cleanFilter: () => dispatch({ type: "CLEAR_FILTERS" })
	};
};

export default connect(
	a,
	b
)(Goods);
