import React from "react";
import { connect } from "react-redux";
import s from "./Menu.module.sass";
import MenuPanel from "./MenuPanel";

const menuITEMS = t => [
	{
		title: t("menu_clothing"),
		alias: "cloth",
		categories: {
			man: [{ title: "Page 2", link: "/page2" }],
			woman: [],
			boys: [],
			girls: []
		}
	},
	{
		title: t("menu_footwear"),
		alias: "footwear",
		categories: {
			man: [
				"Trainers",
				"Tennis Shoes",
				"Skate Shoes",
				"Shoes",
				"Sandals and Flip Flops",
				"Hi Tops",
				"Football Boots",
				"Canvas Shoes"
			],
			woman: [
				"Canvas Shoes",
				"Football Boots",
				"Hi Tops",
				"Sandals and Flip Flops",
				"Shoes"
			],
			boys: [],
			girls: []
		}
	},
	{
		title: t("menu_drones"),
		alias: "drones",
		categories: {
			BRANDS: ["Parrot", "DJI", "Yuneec", "Holy Stone"],
			ACCESSORIES: [
				"Batteries",
				"Battery Chargers",
				"Cameras",
				"Camera Gimbals",
				"Drone Cases"
			]
		}
	},
	{ title: t("menu_books"), alias: "books", categories: {} },
	{ title: t("electronics"), alias: "electronics", categories: {} },
	{ title: t("teste"), alias: "teste", categories: {} }
];

class Menu extends React.PureComponent {
	state = {
		showMenu: false,
		active: 0,
		MenuElementsNew: () => [{ title: "", categories: {} }]
	};

	componentDidMount() {
		this.setState({
			MenuElementsNew: menuITEMS
		});
	}

	handleMemuShow = type => {
		this.setState({ showMenu: type });
	};

	render() {
		const { t, Link, chengeCategory } = this.props;

		const activeCategories = this.state.MenuElementsNew(t)[
			this.state.active
		].categories;

		return (
			<div className={s.Menu}>
				<ul className={s.Wrapper}>
					{this.state.MenuElementsNew(t).map((elem, index) => {
						return (
							<li
								style={{
									background:
										this.state.showMenu &&
										this.state.active === index
											? "#f5f5f5"
											: "#fff"
								}}
								onMouseOver={() => {
									this.setState({
										showMenu: true,
										active: index
									});
								}}
								onMouseLeave={() => {
									this.setState({ showMenu: false });
								}}
								key={index}
							>
								<Link
									to={`/filter/${(
										elem.alias || ""
									).toLowerCase()}`}
									onClick={() =>
										chengeCategory(
											(elem.alias || "").toLowerCase()
										)
									}
								>
									{elem.title}
								</Link>
							</li>
						);
					})}
				</ul>
				<MenuPanel
					activeCategories={activeCategories}
					Link={Link}
					showMenu={this.state.showMenu}
					handleMemuShow={this.handleMemuShow}
				/>
			</div>
		);
	}
}

const a = store => ({
	t: store.translation.t
});
const b = dispatch => {
	return {
		chengeMenu: () => dispatch({ type: "CHENGE_MENU_TYPE" }),
		chengeCategory: category =>
			dispatch({ type: "CHANGE_CATEGORY", payload: category })
	};
};

export default connect(
	a,
	b
)(Menu);
