import React from "react";
import { connect } from "react-redux";
import s from "./Filters.module.sass";
import { InputText } from "primereact/inputtext";
import Menu from "../Menu/";
import { Checkbox } from "primereact/checkbox";
import { Slider } from "primereact/slider";

const fakeSizes = [40, 41, 42];
const fakeBrand = ["Nike", "Adidas"];
const fakeYear = [2017, 2018];

class Filters extends React.Component {
	state = {
		brands: [],
		sizes: [],
		years: [],
		rangeValues: [500, 10000]
	};

	onChangeCheckbox = (type = "brand") => value => event => {
		const findedType = this.state[type];
		const { changeFilters } = this.props;

		let result = [];

		if (findedType.includes(value)) {
			result = findedType.filter(_brand => _brand !== value);
		} else {
			result = [...findedType, value];
		}
		console.log(type, result);
		changeFilters({
			type,
			result
		});
		this.setState({ [type]: result });
	};
	onChangeRangerSlider = type => event => {
		const { changeFilters } = this.props;
		const rv = this.state.rangeValues;

		rv[type] = Number(event.target.value);

		changeFilters({
			type: "rangeValues",
			result: rv
		});

		this.setState({ rangeValues: rv });
	};

	render() {
		const { goodsList } = this.props;
		const brands = [];
		const sizes = [];
		const years = [];

		goodsList.forEach(good => {
			if (!brands.includes(good.brand)) brands.push(good.brand);
			(good.size || []).forEach(_size => {
				if (!sizes.includes(_size)) sizes.push(_size);
			});

			if (!years.includes(good.year)) years.push(good.year);
		});
		return (
			<div className={s.Filters}>
				<h3>Brand</h3>
				<div className={s.brand}>
					{brands.map((brand, key) => {
						return (
							<div key={key}>
								<Checkbox
									onChange={this.onChangeCheckbox("brands")(
										brand
									)}
									checked={this.state.brands.includes(brand)}
								></Checkbox>
								{brand}
							</div>
						);
					})}
				</div>
				<h3>Size</h3>
				<div className={s.size}>
					{sizes.map((size, key) => {
						return (
							<div key={key}>
								<Checkbox
									onChange={this.onChangeCheckbox("sizes")(
										size
									)}
									checked={this.state.sizes.includes(size)}
								></Checkbox>
								{size}
							</div>
						);
					})}
				</div>
				<h3>Year</h3>
				<div className={s.year}>
					{years.map((year, key) => {
						return (
							<div key={key}>
								<Checkbox
									onChange={this.onChangeCheckbox("years")(
										year
									)}
									checked={this.state.years.includes(year)}
								></Checkbox>
								{year}
							</div>
						);
					})}
				</div>
				<h3>Price</h3>
				<div className={s.price}>
					<Slider
						style={{ width: "200px" }}
						min={400}
						max={10000}
						value={this.state.rangeValues}
						onChange={e => this.setState({ rangeValues: e.value })}
						range={true}
						onSlideEnd={e => {
							this.onChangeRangerSlider(0)({
								target: { value: e.values[0] }
							});
							this.onChangeRangerSlider(1)({
								target: { value: e.values[1] }
							});
						}}
					/>
				</div>
				От:{" "}
				<input
					onChange={this.onChangeRangerSlider(0)}
					value={this.state.rangeValues[0]}
					className={s.inputPrice}
				/>
				До:{" "}
				<input
					onChange={this.onChangeRangerSlider(1)}
					value={this.state.rangeValues[1]}
					className={s.inputPrice}
				/>
			</div>
		);
	}
}

const a = store => ({
	goodsList: store.goods.goodsList,
	brand: store.filter.brand,
	years: store.filter.years,
	sizes: store.filter.sizes,
	rangeValues: store.filter.rangeValues
});
const b = dispach => {
	return {
		changeFilters: payload => dispach({ type: "CHANGE_FILTERS", payload })
	};
};

export default connect(
	a,
	b
)(Filters);
