import config from "../config";

const { API_HOST, API_VERSION } = config;
const token = "fsdf";

export default class API {
	constructor(type = "users") {
		this.type = type;
		this.token = token;
		this.URL = `${API_HOST}${API_VERSION ? "/" : ""}${API_VERSION}/${type}`;
	}
	get(path = "", isAuth = false) {
		const headers = {};
		if (isAuth) headers.authorization = `Bearer ${isAuth}`;
		return fetch(`${this.URL}/${path}`, {
			method: "GET",
			headers
		}).then(response => response.json());
	}

	delete(path = "", isAuth = false) {
		const headers = {};
		if (isAuth) headers.authorization = `Bearer ${this.token}`;
		return fetch(`${this.URL})/${path}`, {
			method: "DELETE"
		}).then(response => response.json());
	}

	post(path = "", isAuth = false, body = {}) {
		const headers = {};
		if (isAuth) headers.authorization = `Bearer ${this.token}`;
		if (body) headers["content-type"] = `application/json`;

		return fetch(`${this.URL}/${path}`, {
			method: "POST",
			body: JSON.stringify(body),
			headers
		}).then(response => response.json());
	}

	put(path = "", isAuth = false, body = {}) {
		const headers = {};
		if (isAuth) headers.authorization = `Bearer ${this.token}`;
		if (body) headers["content-type"] = `application/json`;

		return fetch(`${this.URL})/${path}`, {
			method: "PUT",
			body: JSON.stringity(body)
		}).then(response => response.json());
	}
}
