import React from "react";
import { connect } from "react-redux";
import s from "./Header.module.sass";
import { InputText } from "primereact/inputtext";
import Menu from "../Menu/";
import { Dropdown } from "primereact/dropdown";
import localForage from "localforage";
import logoicon from "../../images/logo2.png";
import carticon from "../../images/shop-logo-big.png";

/**
 * eslint-disable
 **/
const languagesSelectItems = [
	{ label: "en", value: "en" },
	{ label: "ru", value: "ru" }
];
const imageLinks = {
	auth: "http://cdn.onlinewebfonts.com/svg/img_276773.png",
	logo: logoicon,
	burger: "https://static.thenounproject.com/png/204478-200.png",
	cart: carticon
};

class Header extends React.Component {
	state = {
		languages: []
	};
	async componentWillMount() {
		const lang = await localForage.getItem("language");

		this.setLanguage(lang || "en");
	}

	setLanguage = language => {
		const { changeLanguage } = this.props;
		localForage.setItem("language", language);

		changeLanguage(language);

		// this.props.actions.changeLanguage(i18next);
	};
	render() {
		const {
			Link,
			cartItems = [],
			changeCart,
			userData,
			t,
			language
		} = this.props;
		return (
			<>
				<header className={s.header}>
					<div className={s.wrapper}>
						<Dropdown
							className={s.languages}
							value={this.state.languages}
							options={languagesSelectItems}
							onChange={event => {
								this.setLanguage(event.value);
								this.setState({ languages: event.value });
							}}
							placeholder={language}
						/>
						<div className="p-inputgroup">
							<span className="p-inputgroup-addon">
								<i className="pi pi-search"></i>
							</span>
							<InputText placeholder={t("header_search")} />
						</div>
						<div className="logo">
							<Link to="/">
								<img src={imageLinks.logo} />
							</Link>
						</div>

						{/*<div
						// 	className={s.burgerIcon}
						// 	onClick={this.props.changeMenu}
						// >
						// 	<img src={imageLinks.burger} />
						// </div>*/}
						<div onClick={() => changeCart()} className={s.cart}>
							<img src={imageLinks.cart} width="100px" />
							<span>{cartItems.length}</span>
						</div>
						<div>
							<Link to={!userData.token ? "/login" : "/profile"}>
								<img src={imageLinks.auth} width="40px" />
							</Link>
						</div>
					</div>

					<Menu Link={this.props.Link} />
				</header>
			</>
		);
	}
}

const a = store => ({
	cartItems: store.cart.preparedGoods,
	userData: store.user,
	language: store.translation.language,
	t: store.translation.t
});
const b = dispach => {
	return {
		changeMenu: () => dispach({ type: "CHANGE_MENU_TYPE" }),
		changeCart: () => dispach({ type: "CHANGE_CART_TYPE" }),
		changeLanguage: language =>
			dispach({ type: "CHANGE_LANGUAGE", payload: language })
	};
};

export default connect(
	a,
	b
)(Header);
