const initialState = {
	showCart: false,
	preparedGoods: []
};

function UserReducer(state = initialState, action) {
	switch (action.type) {
		case "CHANGE_CART_TYPE":
			return { ...state, showCart: !state.showCart };

		case "ADD_GOOD":
			return {
				...state,
				preparedGoods: [...state.preparedGoods, action.payload]
			};
		case "REMOVE_GOOD":
			let checkForOneElement = false;

			console.log(action.payload);

			return {
				...state,
				preparedGoods: state.preparedGoods
					.map(good =>
						// если айди совпадают и checkForOneElement равен фолс
						good.id === action.payload && !checkForOneElement
							? ((checkForOneElement = true), false)
							: good
					)
					.filter(_good => _good)
			};
		default:
			return state;
	}
}
export default UserReducer;
