import React from "react";
import { connect } from "react-redux";
import localForage from "localforage";
import { Redirect } from "react-router-dom";
import AuthService from "../services/Auth";

class Register extends React.Component {
	state = {
		login: "eve.holt@reqres.in",
		password: "pistol"
	};

	handleChangeByKey = key => _value => {
		if (_value.target) _value = _value.target.value;

		console.log(_value);

		this.setState({ [key]: _value });
	};

	sendUserData = async () => {
		try {
			const { login, password } = this.state;
			const { updateUserData } = this.props;
			const options = {
				method: "POST",
				body: JSON.stringify({
					email: login,
					password,
					username: login
				}),
				headers: { "content-type": "application/json" }
			};
			const userData = await AuthService.post("local/register", false, {
				password,
				username: login,
				email: login
			});

			if (!userData.jwt) throw new Error(userData.message);

			userData.token = userData.jwt;

			localForage.setItem("userData", userData);
			updateUserData(userData);
		} catch (error) {
			alert(error.message);
		}
	};

	// fetch("https://reqres.in/api/register", options).then(response =>
	// 	response.json().then(body => {
	// 		if (!body.error) {
	// 			this.setState({ ...body });

	// 			const userNewData = { ...this.state, ...body };

	// 			delete userNewData.password;

	// 			localForage.setItem("userData", userNewData);

	// 			updateUserData({ ...this.state, ...body });
	// 		} else {
	// 			alert(body.error);
	// 		}
	// 	})
	// );
	render() {
		const { login, password } = this.state;
		const { userData = {} } = this.props;

		console.log(this.state);

		return userData.token ? (
			<Redirect to="/profile/" />
		) : (
			<>
				Login:{" "}
				<input
					value={login}
					onChange={this.handleChangeByKey("login")}
				/>
				<br />
				Password:{" "}
				<input
					value={password}
					onChange={this.handleChangeByKey("password")}
				/>
				<br />
				<button onClick={this.sendUserData}>Зарегестрироватся</button>
			</>
		);
	}
}

const a = state => ({
	userData: state.user
});

const b = dispatch => {
	return {
		updateUserData: payload => dispatch({ type: "UPDATE_USER", payload })
	};
};

export default connect(
	a,
	b
)(Register);
