import React from "react";
import { connect } from "react-redux";
import CustomerDetails from "../components/CustomerDetails";
import OrderSummary from "../components/OrderSummary";
import BreadCrumb from "../components/BreadCrumb";
import GoodFoto from "../components/GoodFoto";
import GoodCounter from "../components/GoodCounter";
import GoodDescription from "../components/GoodDescription";

const checkoutStyles = {
	display: "flex",
	flexDirection: "column",
	justifyContent: "space-around"
};
const leftStyles = {
	display: "flex",
	justifyContent: "space-around"
};
class Goodinfo extends React.Component {
	state = {
		login: "",
		password: ""
	};

	handleChangeByKey = key => _value => {
		if (_value.target) _value = _value.target.value;

		console.log(_value);

		this.setState({ [key]: _value });
	};
	render() {
		const { goodsList = {} } = this.props;
		const goodID = window.location.pathname.replace("/good/", "");
		const findedGood = goodsList.find(_good => _good.id === goodID);

		return (
			<>
				{!findedGood ? (
					"Товар не найден!"
				) : (
					<div style={checkoutStyles}>
						<div style={leftStyles}>
							<div>
								<BreadCrumb category={findedGood.category} />
								<GoodFoto photo={findedGood.image} />
							</div>
							<GoodCounter
								good={findedGood}
								price={findedGood.price}
								name={findedGood.name}
							/>
						</div>
						<GoodDescription describe=",,," characteristics="..." />
					</div>
				)}
			</>
		);
	}
}

const a = state => ({
	goodsList: state.goods.goodsList
});
const b = dispatch => {
	return {
		addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
		removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id })
	};
};
export default connect(
	a,
	b
)(Goodinfo);
