import React from 'react';
import Slider from "react-slick";
import s from './Slider.module.sass'

const slides = [
	'https:\/\/store.cezerin.com/assets/images/slide7.jpg',
	'https:\/\/store.cezerin.com/assets/images/slide8.jpg',
	'https:\/\/store.cezerin.com/assets/images/slide9.jpg',
];


class SimpleSlider extends React.Component {
	state = {};

	render () {

		const settings = {
	      dots: true,
	      infinite: true,
	      speed: 1000,
	      slidesToShow: 1,
	      slidesToScroll: 1,
	      autoplay: true,
	    };
    return <Slider {...settings} className={s.Slider}>
    <div>
         <img src={slides[0]} />
    </div>
     <div>
         <img src={slides[1]} />
    </div>
    <div>
         <img src={slides[2]} />
    </div>

    </Slider>;
	}
}

export default SimpleSlider;