import React from "react";
import { connect } from "react-redux";
import localForage from "localforage";
import { Redirect } from "react-router-dom";
import AuthService from "../services/Auth";

class Login extends React.Component {
	state = {
		login: "eve.holt@reqres.in",
		password: "pistol"
	};

	handleChangeByKey = key => _value => {
		if (_value.target) _value = _value.target.value;

		console.log(_value);

		this.setState({ [key]: _value });
	};
	sendUserData = async () => {
		try {
			const { login, password } = this.state;
			const { updateUserData } = this.props;

			const userData = await AuthService.post("local", false, {
				password,
				identifier: login
			});

			if (!userData.jwt) throw new Error(userData.message);

			userData.token = userData.jwt;
			userData.id = userData.user._id;
			console.log(userData);

			localForage.setItem("userData", userData);
			updateUserData(userData);
		} catch (error) {
			alert(error.message);
		}
	};
	render() {
		const { login, password } = this.state;
		const { Link } = this.props;
		const { userData = {} } = this.props;

		return userData.token ? (
			<Redirect to="/profile/" />
		) : (
			<>
				Login:{" "}
				<input
					value={login}
					onChange={this.handleChangeByKey("login")}
				/>
				<br />
				Password:{" "}
				<input
					value={password}
					onChange={this.handleChangeByKey("password")}
				/>
				<br />
				<button onClick={this.sendUserData}>Войти</button>
				<Link to="/register">
					<button>Регистрация</button>
				</Link>
			</>
		);
	}
}
const a = state => ({
	userData: state.user
});

const b = dispach => {
	return {
		updateUserData: payload => dispach({ type: "UPDATE_USER", payload })
	};
};

export default connect(
	a,
	b
)(Login);
