import React from "react";
import s from "./Cart.module.sass";
import { connect } from "react-redux";
import { Sidebar } from "primereact/sidebar";
import { Card } from "primereact/card";
import { Button } from "primereact/button";

const header = src => <img alt="Card" src={src} />;
const footer = ({ removeGood, good }) => (
	<span>
		<Button
			onClick={() => removeGood(good.id)}
			label="Cancel"
			icon="pi pi-times"
			className="p-button-secondary"
		/>
	</span>
);

class Cart extends React.Component {
	state = {};

	changeMenu = () => {
		this.setState({ showCart: !this.state.Cart });
	};

	render() {
		const {
			showCart,
			changeCart,
			cartItems = [],
			removeGood,
			Link
		} = this.props;

		return (
			<>
				<div className={s.Cart}>
					<Sidebar
						style={{ zIndex: 99999999 }}
						position="right"
						visible={showCart}
						onHide={() => changeCart()}
					>
						<nav className={s.burgerMenu}>
							<ul className={s.List}>
								{cartItems.length === 0 ? (
									<li>Пусто</li>
								) : (
									cartItems.map((item, key) => {
										return (
											<li key={key}>
												<Card
													footer={footer({
														removeGood,
														good: item
													})}
													header={header(item.image)}
												>
													{item.name} {item.price}
												</Card>
											</li>
										);
									})
								)}
							</ul>
						</nav>
						<br />
						<b>
							Всего к оплате
							{cartItems.length === 0
								? 0
								: cartItems
										.map(x => Number(x.price))
										.reduce((a, b) => a + b)}
							$
						</b>
						<br />
						<Link to="/checkout">
							<Button
								onClick={this.props.changeCart}
								className={`p-button-warning ${s.Checkout}`}
								label="Оплатить"
								icon="pi pi-check"
							/>
						</Link>
					</Sidebar>
				</div>
			</>
		);
	}
}
const a = state => ({
	showCart: state.cart.showCart,
	cartItems: state.cart.preparedGoods
});
const b = dispach => {
	return {
		changeCart: () => dispach({ type: "CHANGE_CART_TYPE" }),
		removeGood: _id => dispach({ type: "REMOVE_GOOD", payload: _id })
	};
};
export default connect(
	a,
	b
)(Cart);
