const initialState = {
	brands: [],
	sizes: [],
	years: [],
	rangeValues: [500, 10000],
	category: ""
};

function FilterReducer(state = initialState, action) {
	switch (action.type) {
		case "CHANGE_CATEGORY":
			return {
				...state,
				category: action.payload
			};
		case "CHANGE_FILTERS":
			return {
				...state,
				[action.payload.type]: [...action.payload.result]
			};
		case "CLEAR_FILTERS":
			return {
				...state,
				brands: [],
				sizes: [],
				years: [],
				rangeValues: [500, 10000]
			};

		default:
			return state;
	}
}
export default FilterReducer;
