const initialState = {
	goodsList: [
		{
			id: "88685",
			name: "Nike",
			category: "footwear",
			price: "500",
			brand: "Nike",
			year: 2017,
			size: [40, 43, 44, 50],
			sex: "men",
			description: `Кроссовки Nike Air Force 1 Flyknit 2.0 созданы на основе модели AF1, которая господствовала на площадках с 1982 года, с невероятно легкой конструкцией. Потрясающий верх из материала Flyknit и классические линии дизайна AF1 образуют почти невесомую модель в баскетбольном стиле прошлого.`,
			image:
				"https://images.ua.prom.st/1366017685_w640_h640_muzhskie-krossovki-nike.jpg"
		},
		{
			id: "54321",
			name: "Nike",
			category: "footwear",
			price: "600",
			brand: "Adidas",
			year: 2018,
			size: [40, 43, 44],
			sex: "men",
			description: `Кроссовки Nike Air Force 1 Flyknit 2.0 созданы на основе модели AF1, которая господствовала на площадках с 1982 года, с невероятно легкой конструкцией. Потрясающий верх из материала Flyknit и классические линии дизайна AF1 образуют почти невесомую модель в баскетбольном стиле прошлого.`,
			image:
				"https://i2.rozetka.ua/goods/6469475/nike_885177605896_images_6469475318.jpg"
		}
	]
};
function GoodsReducer(state = initialState, action) {
	switch (action.type) {
		case "SET_GOODS":
			return { ...state, goodsList: action.payload };
		default:
			return state;
	}
}
export default GoodsReducer;
