import React from "react";
import s from "./BurgerMenu.module.sass";
import { connect } from "react-redux";
import { Sidebar } from "primereact/sidebar";

class BurgerMenu extends React.Component {
	state = {};

	// changeMenu = () => {
	// 	this.setState({ showMenu: !this.state.showMenu });
	// };

	render() {
		return (
			<>
				<Sidebar
					visible={this.props.showMenu}
					onHide={this.props.changeMenu}
				>
					<nav
						className={s.burgerMenu}
						style={{
							left: this.props.showMenu ? "0px" : "-999px"
						}}
					>
						<ul>
							<li>Домой</li>
							<li>О сайте</li>
							<li>Контакты</li>
						</ul>
					</nav>
				</Sidebar>
			</>
		);
	}
}

const a = state => ({
	showMenu: state.burgerMenu.showMenu
});

const b = dispach => {
	return {
		changeMenu: () => dispach({ type: "CHANGE_MENU_TYPE" })
	};
};

export default connect(
	a,
	b
)(BurgerMenu);

